@import "./styles/colours";
@import "./styles/variables";

body {
  margin: 0;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

@media (max-width: 1000px) {
  .App {
    overflow-x: auto;
    min-width: 1000px;
  }
}
.Timeline {
  margin-bottom: $baseline;
  border-radius: 4px;
  background-color: $nb-white;
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
  @include drop-shadow();
}

.Scrubber + .Timeline {
  margin-top: $baseline;
}

.TimelineSummary {
  background-color: $nb-grey-mid-dark;
}

#metrics-scrubber {
  margin-top: 2px;
}

.hideableContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
