@import 'colours';

.appContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  margin: 0 20px;
}

.container:nth-child(2) {
  margin-bottom: 10px;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -12px;
  margin-top: -12px;
  background-image: url('../assets/svgs/loading-spinner.svg');
}

.noAgentDataText {
  position: relative;
  top: 50%;
  text-align: center;
  margin-left: 0;
  margin-top: -12px;
  z-index: 99;
  color: #5c6770;
}

.noAgentDataText.notes {
  margin-left: 16px;
  margin-right: 16px;
}
.loading-overlay {
  background-color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
}

.fade-out {
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
}

.fade-out,
.hidden {
  visibility: hidden;
}

* {
  font-size: 12px;
  line-height: 1.5;
}

.bg-primary {
  background-color: $nb-primary-channel;
}

.bg-secondary {
  background-color: $nb-secondary-channel;
}

.bg-grey-mid {
  background-color: $nb-grey-light;
}

.text-white {
  color: $nb-white;
}

.text-grey-primary {
  color: $nb-grey-primary;
}

.translate-50, .translate-l50 {
  transform: translate(-50%);
}

.translate-r50 {
  transform: translate(50%);
}

.translateV-50 {
  transform: translate(0, -50%);
}

button:focus {
  outline:0;
}

.button {
  border-radius: 4px;
  padding: 8px 16px;
}

.button--state-outline {
  background: unset;
  border: solid 1px $nb-white;
  color: $nb-white;
}

.button--state-primary {
  background-color: $nb-green-light;
  border: unset;
  color: $nb-grey-mid-dark;
  font-size: 13px;
  font-weight: bold;
}

hr {
  border-color: $nb-grey-mid-dark;
}

.column-container {
  display: flex;
}

.column {
  flex: 1;
}

.vertical-bar {
  background-color: $nb-grey-mid-dark;
}

.vertical-bar,
.vertical-spacer {
  margin: 0 16px;
  width: 2px;
}
