@import "../../styles/colours";

.onTop {
  z-index: 11;
}

.TranscriptionConversation {
  position: absolute;
  display: flex;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  padding: 0;
  margin: 0;
}

.transcriptionEventContainer {
  width: 100%;
  padding: 0;
  margin: 0;
}

.transcriptionEventContainer,
.transcriptionEventContainer > * {
  position: absolute;
  pointer-events: none;
}

.container {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  box-sizing: border-box;
}

.playHead {
  align-items: center;
  position: absolute;
  margin-top: -10px;
  left: 12px;
  z-index: 0;
  pointer-events: none;
}

.start {
  align-items: center;
  text-align: center;
  padding-left: 38px;
  z-index: 10;
}

.conversation {
  margin-left: 42px;
  height: 100%;
};

.conversationContainer {
  width: 100%;
  padding: 10px 12px 18px 0;
}

.transcriptionEventContainer,
.conversationContainer {
  margin-top: 48px;
}

.conversationRow {
  padding: 0 0 0 10px;
  position: relative;
  width: 100%;
  z-index: 1;
  display: flex;

}

.conversationRowLinked {
  display: flex;
  justify-content: space-between;
}

.conversationRowRight {
  display: flex;
  justify-content: flex-end;
}

.playheadLocation {
  /* Used as an identification class */
}

.conversationRow.spacer {
  height: 12px;
}

.conversationRow .sentence {
  position: relative;
  background-color: transparent;
  pointer-events: none;
  @media (max-width: 1350px) {
    box-sizing: border-box;
  }
}

.sentence {
  display: flex;
  color: $nb-grey-primary;
  width: 46%;
  word-break: break-word;
}

.sentence.primary {
  /* Used as an identification class */
  margin-left: 28px;
}

.sentence.secondary {

}

.sentence.secondaryLinked {
  // left: 0;
}

.playheadMarker {
  position: absolute;
  height: 1px;
  background-color: $nb-green-dark;
  left: 0;
  width: 100%;
  z-index: 9;
}

.sentenceBody {
  background-color: $nb-grey-lightest2;
  padding: 4px 4px 4px 4px;
  width: 100%;
  border-radius: 0 4px 4px 4px;
  min-height: 20px;
  filter: drop-shadow(0 1px 2px rgba(0,0,0,0.25));
  z-index: 0;
}

.person {
  margin: 0 5px;
  position: relative;
  width: 50%;
  box-sizing: border-box;
}

.person > div {
  display: flex;
  filter: drop-shadow(0 1px 4px rgba(0,0,0,0.25));
  margin: 0;
  padding: 4px;
  position: absolute;
  left: 0;
  right: 0;
}

.sentence {
  background-color: $nb-grey-lightest2;
  border-radius: 0 4px 4px 0;
  margin: 0;
  padding: 4px 4px 4px 8px;
  line-height: 1.5;
  right: 0;
  min-height: 18px;
}

.blanking {
  width: 4px;
  z-index: 2;
  margin-left: 0;
  margin-right: -4px;
  height: 26px;
  background-color: $nb-grey-lightest2;
}

.secondary .blanking {
  margin-left: -4px;
  margin-right: 0;
}

.initials {
  background-color: $nb-grey-lightest2;
  border-radius: 4px 0 0 4px;
  margin: 0;
  padding: 4px 4px 0 4px;
  width: 28px;
  height: 18px;
  text-align: center;
  filter: drop-shadow(-1px 1px 2px rgba(0,0,0,0.25));
  z-index: 1;
  pointer-events: none;
}

.sentence.secondary .sentenceBody {
  border-radius: 4px 0 4px 4px;
  padding: 4px 8px 4px 4px;
}

.sentence.secondary .initials {
  border-radius: 0 4px 4px 0;
  filter: drop-shadow(1px 1px 2px rgba(0,0,0,0.25));
}

.playHeadLine {
  flex-grow: 1;
  height: 2px;
  margin-left: 2px;
  margin-right: -24px;
  background-color: $nb-grey-primary;
}

.playHeadStartText {
  background-color: $nb-white;
  padding: 0 8px;
}

.confidence {
  background-image: url('../../assets/svgs/confidence-highlight.svg');
  background-repeat: repeat-x;
  background-position: left bottom;
  padding-bottom: 2px;
}

.matchFill {
  border-radius: 2px;
  background-position: 0 0;
  display: inline;
  transition: 1s ease-in-out;
  padding: 2px;
  margin: -2px;
}

.matchFillAnimate {
  background-position: -100% 0 !important;
}
