@import "../../styles/colours";

.content {
  color: $nb-grey-primary;
}

.column {
  width: 50%;
}

hr {
  margin-block-end: 0;
  border: 1px solid $nb-grey-mid-dark;
  border-radius: 1px;
}

.title {
  font-size: 12px;
  margin-top: 0;
  margin-bottom: 6px;
}

.topics {
  margin-top: 16px;
}

.spacer {
  width: 16px;
}

.pillContainer {
  display: flex;
  flex-wrap: wrap;
  max-height: 70px;
  overflow: hidden;
}

.pillContainer > div {
  margin-top: 8px;
}

.classificationContainer {
  display: flex;
  flex-wrap: wrap;
  max-height: 70px;
  overflow: hidden;
}

.classificationContainer > div {
  margin-top: 8px;
}

.classificationContainer > div:not(:last-child) {
  margin-right: 4px;
}

.classificationsExceptionHeader {
  display: flex;
  justify-content: space-between;
}

.classificationsExceptionColumn {
  display: flex;
  flex-direction: column;
}

.classificationsExceptionColumn hr {
  margin: 0;
}

.classificationsEmpty {
  align-items: center;
  display: flex;
  flex-grow: 1;
  font-size: 11px;
  margin: 0 48px;
  text-align: center;
}

.noPointerEvents {
  pointer-events: none;
}

.searchNavigation {
  display: flex;
  padding: 0;
  align-items: center;
  position: relative;
  color: white;
  float: right;
  margin-top: -5px;
}

.searchNavigation > * {
  margin: 0 16px 0 0;
}

.searchNavigation button {
  border: 0;
  background: transparent;
  padding: 0;
}

.showAll {
  margin-top: -2px;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  background: transparent;
  color: $nb-grey-primary;
  margin-bottom: 6px;
  outline: none;
  padding: 0 8px 0 8px;
  float: right;
}

.progressBarContainer {
  width: 100%;
  position: absolute;
  top: 5px;
  text-align: center;
  font-size: 10px;
  color: $nb-grey-primary;
  display: flex;
  flex-direction: column;
  justify-content: center;
  place-items: center;
}

.progressBarOuter {
  margin: 0;
  width: 164px;
  height: 8px;
  border-radius: 4px;
  border: solid 1px $nb-grey-primary;
  background-color: $nb-grey-primary;
  display: flex;
  place-items: center;
}

.progressBar {
  box-sizing: border-box;
  padding: 1px;
  height: 7px;
  border-radius: 3px;
  border: solid 1px $nb-green-light;
  background-color: $nb-green-light;
}
