@import "../../styles/colours";
@import "../../styles/variables";

.Player {
  display: flex;
  align-items: center;
}

.Player button:focus {
  outline:0;
}

.Player > button:not(:last-child) {
  margin-right: 12px;
}

.skipBack,
.playback,
.skipForward,
.play,
.playDisabled,
.pause {
  background-size: 100%;
  background-position: center;
  background-color: transparent;
}

.skipBack,
.playback,
.skipForward {
  border: 0;
  height: 36px;
  padding: 0;
  width: 36px;
}

.play,
.playDisabled,
.pause {
  border: 0;
  height: 40px;
  padding: 0;
  width: 40px;
}

.skipBack {
  background-image: url('../../assets/svgs/skip-back.svg');
}

.play {
  background-image: url('../../assets/svgs/play.svg');
}

.playDisabled {
  background-image: url('../../assets/svgs/play-disabled.svg');
}

.playback {
  background-image: url('../../assets/svgs/playback.svg');
}

.pause {
  background-image: url('../../assets/svgs/pause.svg');
}

.skipForward {
  background-image: url('../../assets/svgs/skip-forward.svg');
}

.changeSpeed {
  font-size: 16px;
  text-align-last: right;
  color: $nb-grey-darkest;
  border: 0;
}

.changeSpeed:focus {
  outline: none;
}

.spinerLoading {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  margin-right: 12px;
}
.spinerLoading div {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  position: absolute;
  width: 40px;
  height: 40px;
  border: 5px solid gray;
  border-radius: 50%;
  animation: spinerLoading 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: gray transparent transparent transparent;
}
.spinerLoading div:nth-child(1) {
  animation-delay: -0.45s;
}
.spinerLoading div:nth-child(2) {
  animation-delay: -0.3s;
}
.spinerLoading div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes spinerLoading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.progress {
  color: gray;
  position: absolute;
  font-size: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
