@import "../../styles/colours";

.container {
  color: $nb-grey-primary;
}

.title {
  font-size: 12px;
  margin-top: 0;
  margin-bottom: 6px;
}

label {
  font-size: 12px;
  line-height: 1.5;
}

.subtitle {
  display: block;
  font-size: 10px;
  margin-left: 24px;
}

.formCheck {
  display: flex;
  margin-left: -4px;
}

.formCheck > * {
  flex: 1;
}

.formCheck:not(:last-child) {
  margin-bottom: 8px;
}

.indicator {
  border-radius: 9px;
  color: $nb-grey-primary;
  display: flex;
  font-size: 12px;
  line-height: 1.5;
  overflow: hidden;
  text-align: center;
  width: 100px;
}

.separator {
  margin-bottom: 8px;
}

.formCheck input[type="checkbox"] {
  outline: none;
  float: left;
  appearance: none;
  margin: 1px 8px 0 0;
  border: 2px solid $nb-grey-lighter;
  padding: 5px;
  border-radius: 2px;
  display: inline-block;
  position: relative;
}

.formCheck input[type="checkbox"]:checked {
  border: 2px solid $nb-grey-mid-light;
}

.formCheck input[type="checkbox"]:checked:after {
  content: '';
  position: absolute;
  width: 6px;
  height: 3px;
  top: 2px;
  left: 1px;
  border: 2px solid $nb-grey-mid-light;
  border-top: none;
  border-right: none;
  border-radius: 1px;
  -webkit-transform: rotate(-50deg);
  -moz-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  transform: rotate(-50deg);
}

.formCheck input[type="checkbox"]::after {
  clear: left;
}

.thirdWidth,
.halfWidth {
  flex-shrink: 1;
  flex-grow: 0;
}

.halfWidth {
  flex-basis: 50%;
}

.thirdWidth {
  width: 33%;
}
