@import "../../styles/colours";

.mask {
  background-color: rgba(73, 83, 92, 0.5);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
}

.modal {
  background: $nb-grey-extra-light;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  left: 50%;
  padding: 24px;
  position: fixed;
  top: 15%;
  transform: translate(-50%, 0);
  width: 100%;
}

.modalHeader {
  color: $nb-grey-primary;
  margin-bottom: 12px;
  text-align: center;
}

.modalHeader .title {
  height: 20px;
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 4px;
  color: #5c6770;
}

.message {
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #5c6770;
}

.modalFooter {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
  padding-top: 24px;
}

@media (min-width: 576px) {
  .modal {
    max-width: 568px;
  }
}

@media (min-width: 768px) {
  .modal {
    max-width: 630px;
  }
}

@media (min-width: 992px) {
  .modal {
    max-width: 715px;
  }
}

.secondaryButton {
  background: transparent;
  border: 0;
  color: $nb-blue-mid;
  margin-right: 24px;
  outline: none;
}

.primaryButton {
  border: none;
  background: $nb-green-light;
  border-radius: 4px;
  color: $nb-grey-mid-dark;
  font-weight: 500;
  height: 32px;
  min-width: 113px;
}

.primaryButton:disabled {
  background: $nb-grey-mid-25;
}