@import "../../styles/colours";

.mask {
  background-color: rgba(73, 83, 92, 0.5);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  display: flex;
  justify-content: center;
  place-items: center;
}

.modal {
  background: $nb-grey-extra-light;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  padding: 24px;
  width: 100%;
  max-height: 70%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.modalHeader {
  color: $nb-grey-primary;
  margin-bottom: 12px;
  text-align: center;
}

.modalHeader .title {
  height: 20px;
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 4px;
  color: #5c6770;
}

.subtitle {
  color: #5c6770;
}

hr {
  color: #5c6770;
}

.modalFooter {
  border-top: 2px solid $nb-grey-light;
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
  padding-top: 24px;
}

@media (min-width: 576px) {
  .modal {
    max-width: 568px;
  }
}

@media (min-width: 768px) {
  .modal {
    max-width: 630px;
  }
}

@media (min-width: 992px) {
  .modal {
    max-width: 715px;
  }
}

.columnContainer {
  display: flex;
}

.columnContainer > div {
  flex: 1;
}

.columnContainer > div:not(:last-child) {
  margin-right: 20px;
}

.columnContainer .title {
  color: $nb-grey-primary;
  margin-bottom: 8px;
}

.columnContainer hr {
  margin-block-end: 0;
  border: 1px solid $nb-grey-light;
  border-radius: 1px;
}

.pillContainer {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
}

.classificationPill {
  align-items: center;
  border: 2px solid;
  border-radius: 8px;
  display: flex;
  margin-top: 42px;
  overflow: hidden;
}

.classificationPill.light {
  background-color: $nb-grey-extra-light;
  border-color: $nb-grey-light;
  color: $nb-grey-primary;
}

.classificationPill.dark {
  background-color: $nb-grey-mid-dark;
  border-color: $nb-grey-mid-dark;
  color: $nb-white;
}

.classificationPill:focus {
  outline: none;
}

.classificationPill[data-metric-selected=true] {
  background-color: $nb-grey-lightest;
  border-color: $nb-grey-light;
  color: $nb-grey-primary;
}

.pillText {
  margin: 4px 8px;
  pointer-events: none;
}

.classificationIndicator {
  background-color: $nb-grey-mid-dark;
  border-radius: 3px;
  height: 6px;
  margin-left: 8px;
  min-width: 6px;
  width: 6px;
}

.classificationIndicator.red {
  background-color: $nb-red;
}

.classificationIndicator.orange {
  background-color: $nb-orange;
}

.classificationIndicator.green {
  background-color: $nb-green-light;
}

.classificationIndicator.grey {
  background-color: $nb-grey-light;
}

.closeButton {
  border: none;
  background: $nb-green-light;
  border-radius: 4px;
  color: $nb-grey-mid-dark;
  font-weight: 500;
  height: 32px;
}

.closeButton:disabled {
  background: $nb-grey-mid-25;
}

.cancelButton {
  background: transparent;
  border: 0;
  color: $nb-blue-mid;
  margin-right: 24px;
  outline: none;
}
