@import "../../styles/colours";
@import "../../styles/variables";

.callListContainer {
  width: 100%;
  height: 300px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.callListContainer .callListItem:nth-child(odd) {
  background-color: $nb-grey-light;
}

.callListContainer .callListItem:nth-child(even) {
  background-color: $nb-grey-mid-light;
}

.callListItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.callListItem div {
  padding: 12px;
  color: $nb-black;
  text-align: center;
}

.callListItem .uuid {
  width: 300px;
}

.callListItem .itemIndex {
  width: 40px;
  border-right: 2px dotted $nb-grey-primary;
}

.callListItem .uuid a {
  text-decoration: none;
  font-weight: bold;
  color: $nb-black;
}

.callListItem .uuid a:hover {
  color: $nb-grey-primary;
}

.callListItem .uuid a:active,
.callListItem .uuid a:visited {
  color: $nb-green-dark;
}

.Header {
  background-color: $nb-white;
  padding: 9px 0;
  margin-bottom: -2px;
}

.HeaderContainer {
  display: flex;
  align-items: center;
}

.HeaderContainer > * {
  display: flex;
  align-items: center;
  width: 25%;
}

.Branding {
  display: flex;

}

.Branding .logo {
  height: 14px;
}

.Player {
  justify-content: center;
  width: 50%;
}

.LayoutOptions {
  justify-content: flex-end;
}

.Exit {
  justify-content: flex-end;
}

.Exit > *:not(:last-child) {
  margin-right: 8px;
}

.visibilityButton {
  justify-content: center;
}

.dropdownContent {
  display: block;
  position: absolute;
  z-index: 1;
  top: 50px;
  border-radius: 4px;
  border: solid 1px #5c6770;
  background-color: #5c6770;
  box-shadow: 2px 2px 2px #49535c;
}

.regularCheckbox {
  -webkit-appearance: none;
  border: 2px solid #FFFFFF;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05);
  padding: 7px;
  border-radius: 3px;
  display: inline-block;
  position: relative;
  margin-right: 8px;
}

.regularCheckbox:checked {
  border: 2px solid #FFFFFF;
  color: #ffffff;
}

.regularCheckbox:checked:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 4px;
  top: 3px;
  left: 1px;
  border: 2px solid #ffffff;
  border-top: none;
  border-right: none;
  border-radius: 1px;
  -webkit-transform: rotate(-50deg);
  -moz-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  transform: rotate(-50deg);
}

.dropdownItem {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.finish {
  margin-left: 16px;
  width: 163px;
}
