@import "../../styles/colours";

.filter {
  cursor: pointer;
  width: 100%;
  background-color: $nb-white;
  border: 1px solid $nb-grey-light;
  border-radius: 4px;
  padding: 8px;
  position: relative;
  background-image: url('../../assets/svgs/down-chevron-grey.svg');
  background-position: right center;
  background-repeat: no-repeat;

  .popover {
    display: none;
    position: absolute;
    background-color: $nb-white;
    flex-direction: column;
    z-index: 1000;
    width: 100%;
    margin-left: -8px;
    margin-top: 6px;
    box-shadow: 0 8px 8px rgba(0, 0, 0, 0.1);

    .item {
      padding: 8px 12px;

      &:hover {
        background-color: $nb-grey-extra-light;
      }
    }
  }

  &:focus {
    outline: none;
    background-image: url('../../assets/svgs/up-chevron-grey.svg');

    .popover {
      display: flex;
    }
  }
}

.highlighted {
  color: $nb-red;
}
