@import "../../styles/colours";

.button {
  cursor: pointer;
  margin: 0 !important;
  width: 24px;
  height: 24px;
  background-image: url('../../assets/svgs/icon-overflow-menu.svg');
  background-color: $nb-grey-mid-light;
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 0 2px 0 2px;

  &.locked {
    background-image: url('../../assets/svgs/icon-locked.svg');
    background-size: 10px;
  }

  .popover {
    display: none;
    z-index: 1000;
    position: fixed;
    border-radius: 4px;
    margin-left: 28px;
    box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.15);
  }

  &:focus {
    outline: none;

    .popover {
      display: flex;
    }
  }
}

.menu {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  overflow: hidden;
  max-width: 400px;
  background-color: $nb-grey-mid-light;

  .item {
    padding: 8px 12px;
    color: $nb-white;
    font-size: 10px;

    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
    }
  }
}
