@import "../../styles/colours";

.mask {
  background-color: rgba(73, 83, 92, 0.5);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  display: flex;
  justify-content: center;
  place-items: center;
}

.modal {
  background: $nb-grey-extra-light;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.5);
  padding: 24px;
  width: 100%;
  max-height: 70%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.modalHeader {
  color: $nb-grey-primary;
  margin-bottom: 12px;
  text-align: center;
}

.modalHeader .title {
  height: 20px;
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 4px;
}

.modalFooter {
  border-top: 2px solid $nb-grey-light;
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
  padding-top: 24px;
}

@media (min-width: 576px) {
  .modal {
    max-width: 568px;
  }
}

@media (min-width: 768px) {
  .modal {
    max-width: 630px;
  }
}

@media (min-width: 992px) {
  .modal {
    max-width: 715px;
  }
}

.categoriesContainer {
  overflow: auto;
  margin-top: 28px;
}

.categoriesHeader, .categoriesBody {
  display: flex;
  
}

.categoriesHeader {
  position: sticky;
  top: 0;
  background: $nb-grey-extra-light;
}

.headerTitle, .pillContainer {
  flex: 1 1;
}

.headerTitle:not(:last-child), .pillContainer:not(:last-child) {
  margin-right: 20px;
}

.categoriesContainer .title {
  color: $nb-grey-primary;
  margin-bottom: 8px;
}

.categoriesContainer hr {
  margin-block-end: 0;
  border: 1px solid $nb-grey-light;
  border-radius: 1px;
}

.pillContainer {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}

.pillContainerKeywords {
  display: flex;
  align-items: center;
  margin-left: 8px;
}

.selectedItemName {
  color: $nb-grey-primary;
  text-decoration: underline;
}

.triggers {
  display: flex;
  align-items: baseline;
  color: $nb-grey-primary;
  margin-bottom: 8px;
}

.pillContainer > div {
  margin-top: 8px;
}

.closeButton {
  border: none;
  background: $nb-green-light;
  border-radius: 4px;
  color: $nb-grey-mid-dark;
  font-weight: 500;
  height: 32px;
}

.closeButton:disabled {
  background: $nb-grey-mid-25;
}

.cancelButton {
  background: transparent;
  border: 0;
  color: $nb-blue-mid;
  margin-right: 24px;
  outline: none;
}
