@import '../../styles/colours';

.LayerContent {
  height: 100%;
  padding: 0;
  margin: 0;
}

.LayerContent,
.LayerContent > * {
  position: absolute;
}

.LayerContentVertical {
  height: 100%;
  width: 100%;
  padding: 0 0 0 18px;
  margin: 18px 0 0 0;
  overflow-y: auto;
  box-sizing: border-box;
}

.LayerContentVertical,
.LayerContentVertical > * {
  position: absolute;
}

.eventContainer {
  width: 100%;
  padding: 0;
  margin: 0;
  position: absolute;
  pointer-events: none;
}

.ClashLine {
  top: 0;
  height: 100%;
  width: 2px;
}

.ClashLineHorizontal {
  height: 2px;
  width: 100%;
}

.ClashIcon {
  top: 0;
  height: 24px;
  width: 24px;
}

.ClashIcon.Primary circle {
  stroke: $nb-primary-channel;
}

.ClashIcon.Secondary circle {
  stroke: $nb-secondary-channel;
}

.ClashIcon.Cluster circle {
  stroke: $nb-grey-light;
}
