@import "../../styles/colours";
@import "../../styles/variables";

.info-box,
.key-info-box {
  padding-left: 0.5rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}

.key-info-box {
  padding-left: 0.5rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  align-items: stretch;
}

.key-info-box > * {
  padding-left: 2rem;
  background-repeat: no-repeat;
  background-position: left center;
}

.key-info-box .transcription-confidence {
  background-image: url('../../assets/svgs/confidence.svg');
}

.key-info-box .background-noise-level {
  background-image: url('../../assets/svgs/noise.svg');
}

.LayerContent {
  position: absolute;
  height: 100%;
  padding: 0;
  margin: 0;
}

.plotline {
  fill: none;
  stroke-width: 2px;
  vector-effect: non-scaling-stroke;
}

.confidence {
  stroke: $nb-grey-light;
}

.caller {
  stroke: $nb-orange;
  stroke-dasharray: 4px;
}

.agent {
  stroke: $nb-blue-light;
  stroke-dasharray: 4px;
}

.noise {
  fill: $nb-grey-light;
}

.noise-primary {
  stroke: $nb-yellow;
  stroke-width: 2;
  fill: $nb-yellow-light-25;
}

.noise-secondary {
  stroke: $nb-blue-mid;
  stroke-width: 2;
  fill: $nb-blue-lighter-25;
}