@import "../../styles/colours";
@import "../../styles/variables";

.transcriptionContainer {
  position: relative;
  flex-grow: 1;
  min-height: 300px;
}

.Transcription {
  color: $nb-grey-primary;
  font-size: 12px;
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  gap: 10px;
}

.sections {
  background: $nb-white;
  border-radius: 4px;
  @include drop-shadow();
  width: 50%;
}

.LayerContent {
  outline: none;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

.LayerContent,
.LayerContent > * {
  position: absolute;
  pointer-events: none;
}

.playScrub {
  position: absolute;
  width: 100%;
  z-index: 9;
}

.playScrubLine {
  position: absolute;
  top: 11px;
  left: 0;
  height: 2px;
  width: 100%;
  margin-left: 23px;
  opacity: 0.5;
}

.playButton {
  pointer-events: none;
  width: 24px;
  height: 24px;
  z-index: 11;
}

.playHead {
  height: 2px;
  width: 100%;
  margin-top: -1px;
  background-color: $nb-black;
}

.notes {
  box-sizing: border-box;
  float: left;
  height: 100%;
  border-right-width: 2px;
  border-right-color: $nb-grey-light;
  border-right-style: solid;
  overflow: auto;
}

.conversation {
  float: left;
  position: relative;
  height: 100%;
  padding: 0;
  margin: 0;
}

.confidenceContainer {
  position: absolute;
  width: 192px;
  height: 24px;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  background-color: $nb-grey-lightest2;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  border-radius: 0 0 0 24px;
  padding: 1px 8px 1px 16px;
  z-index: 10;
}
.confidenceHighlight {
  text-decoration: underline wavy $nb-brown-grey;
}

.confidenceText {
  font-size: 12px;
  align-content: center;
  padding-left: 4px;
  padding-right: 14px;
}

.slider {
  -webkit-appearance: none;
  width: 48px;
  height: 2px;
  border-radius: 1px;
  background: $nb-grey-light;
  outline: none;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 4px solid $nb-green-light;
  background: $nb-white;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 4px solid $nb-green-light;
  background: $nb-white;
  cursor: pointer;
}
