@import "../../styles/colours";

.actionButton,
.overflowMenu {
  position: absolute;
  right: 0;
}

.overflowMenu {
  top: 0;
}

.actionButton {
  cursor: pointer;
  bottom: 0;
  width: 24px;
  height: 24px;
  background-image: url('../../assets/svgs/icon-tick.svg');
  background-color: $nb-grey-mid-light;
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 0 2px 0 2px;
  margin: 0 !important;

  &.acknowledged {
    background-color: $nb-green-dark;
  }

  &:focus {
    outline: none;
  }
}

.toggleThreadButton {
  cursor: pointer;
  text-align: right;
  text-transform: capitalize;
  font-size: 12px;
  margin: 2px 8px 12px 8px;
  color: $nb-blue-mid;
}

.edited {
  font-weight: normal;
}

.locked {
  background-image: url('../../assets/svgs/icon-locked.svg');
}

.note {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: $nb-grey-mid-25;
  margin: 8px;
  border-radius: 4px;
  outline: none;
  min-height: 64px;
  font-size: 10px;
  padding-right: 24px;

  &.comment {
    margin-left: 48px;
  }

  .closed {
    background-color: $nb-grey-light;
    text-transform: capitalize;
    text-align: center;
    color: $nb-white;
    height: 24px;
    margin-right: -24px;
    line-height: 24px;
    border-radius: 0 0 4px 0;
  }

  .heading,
  .subject {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    font-weight: 500;
    color: $nb-grey-mid-dark;
    margin: 8px 8px 4px 8px;
  }

  .heading {
    justify-content: space-between;

    .author {
      flex-grow: 1;
      overflow-wrap: anywhere;
    }

    .time {
      padding: 0 8px;
    }
  }

  .subject {
    .time {
      color: white;
      background-color: $nb-grey-light;
      padding: 0 2px;
      margin-right: 4px;
    }
  }
}

.noteText {
  flex-grow: 1;
  overflow: hidden;
  margin: 0 8px 8px 8px;
}

.unread {
  position: absolute;
  top: -5px;
  right: -5px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $nb-red;
}
