@import "../../styles/colours";

.classificationPill {
  align-items: center;
  border: 2px solid $nb-grey-lightest;
  border-radius: 8px;
  height: 20px;
  padding: 1px;
  display: flex;
  overflow: hidden;
  background-color: $nb-grey-lightest;
}

.classificationPill.dark {
  background-color: $nb-grey-mid-dark;
  border-color: $nb-grey-mid-dark;
  color: $nb-white;
}

.classificationPill:focus {
  outline: none;
}

.classificationPill[data-metric-selected=true] {
  color: $nb-white;
  border-color: $nb-grey-darkest;
  background-color: $nb-grey-darkest;
}

.classificationIndicator {
  background-color: $nb-grey-mid-dark;
  border-radius: 10px;
  height: 20px;
  min-width: 6px;
  width: 20px;
}

.classificationIndicator.positive {
  background: $nb-green-dark url("../../assets/svgs/icon-small-good.svg") no-repeat center;
}

.classificationIndicator.negative {
  background: $nb-red url("../../assets/svgs/icon-small-error.svg") no-repeat center;
}

.classificationIndicator.warning {
  background: $nb-grey-light url("../../assets/svgs/icon-small-warn.svg") no-repeat center;
}

.classificationIndicator.info {
  background: transparent url("../../assets/svgs/icon-small-info.svg") no-repeat center;
}

.classificationPill[data-metric-selected=true] .classificationIndicator.info {
  background: transparent url("../../assets/svgs/icon-small-info-selected.svg") no-repeat center;
}

.pillText {
  line-height: 1;
  margin: 4px 8px 4px 3px;
  pointer-events: none;
}
