@import "../../styles/colours";
@import "../../styles/variables";

.Search {
  height: 100%;
  display: flex;
}

.Search > * {
  color: $nb-grey-primary;
}

.searchTextBox {
  flex-grow: 1;
  border: 0;
  outline: none;
  caret-color: $nb-grey-primary;
  height: 18px;
}

.searchTextBox::placeholder {
  color: $nb-grey-primary;
  opacity: 1;
}

.searchNavigation {
  display: flex;
  padding: 0;
  align-items: center;
}
.searchNavigation > * {
  margin: 0 16px 0 0;
}

.chevron {
  filter: invert(100%);
}

.spacer {
  height: 100%;
  width: 0;
  border: 1px solid $nb-grey-light;
  box-sizing: border-box;
}

.clearButton {
  color: $nb-grey-primary;
  background-color: $nb-white;
  border: none;
  cursor: pointer;
  outline: none;
}
