@import "../../styles/colours";

.container {
  user-select: none;
  cursor: pointer;
}

.button {
  border-radius: 4px;
  border: 1px solid $nb-grey-light;
  padding: 8px 16px 8px 16px;
  color: $nb-grey-mid-dark;
  width: 80px;
  text-align: center;
}

.popover {
  display: none;
  z-index: 1000;
  border-radius: 4px;
  background-color: $nb-white;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.15);
  flex-direction: column;
  position: fixed;

  .item {
    padding: 4px 12px 4px 4px;
    display: inline-flex;
    align-items: center;
  }

  label {
    color: $nb-grey-primary;
  }
}

.regularCheckbox {
  -webkit-appearance: none;
  border: 2px solid $nb-grey-light;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05);
  padding: 7px;
  border-radius: 3px;
  display: inline-block;
  position: relative;
  margin-right: 8px;
  outline: none;
}

.regularCheckbox:checked {
  border: 2px solid $nb-grey-primary;
  color: #ffffff;
}

.regularCheckbox:checked:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 4px;
  top: 3px;
  left: 1px;
  border: 2px solid $nb-grey-primary;
  border-top: none;
  border-right: none;
  border-radius: 1px;
  -webkit-transform: rotate(-50deg);
  -moz-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  transform: rotate(-50deg);
}
