@import "../../styles/colours";
@import "../../styles/variables";

.resultNavigator {
  align-items: center;
  background-color: $nb-white;
  border-radius: 8px;
  color: $nb-grey-primary;
  display: flex;
  height: 30px;
  margin-right: 16px;
  width: 320px;
  @include drop-shadow;
}

.button {
  background: none;
  border: 0;
}

.button:focus {
  outline:0;
}

.button > img {
  vertical-align: middle;
}

.searchItemContainer {
  display: flex;
  flex: 2;
  margin-right: 8px;
  overflow: hidden;
  padding: 2px 0;
}
