@import "../../styles/colours";
@import "../../styles/variables";

.SearchFilters {
  position: relative;
  margin-top: $baseline;
}

.tabs {
  display: flex;
}

.tabs .button {
  background: none;
  border: 0;
  border-radius: 4px 4px 0 0;
  color: $nb-grey-mid-dark-2;
  font-size: 12px;
  padding: 8px 16px 8px 12px;
}

.tabs .button:focus {
  outline:0;
}

.tabs .button.active {
  background: $nb-white;
  color: $nb-grey-darkest;
  font-weight: 700;
  @include drop-shadow
}

.tabs .button[id=tab-search] {
  width: 42px;
  padding: 8px 12px;
  text-align: center;
  text-indent: -9999em;
  text-transform: uppercase;
  background-image: url('../../assets/svgs/icon-search-grey.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
  background-origin: border-box;
}

.tabs .button.inuse {
  background-image: url('../../assets/svgs/icon-inuse.svg');
  background-repeat: no-repeat;
  background-position: right 22px top 6px;
}

.tabs .spacer {
  flex: 2;
}

.search, .callSummary, .knowledge, .insights {
  background: $nb-white;
  border-radius: 4px;
  padding: 16px;
  margin-bottom: $baseline;
  position: relative;
  z-index: 1;
  @include drop-shadow
}

.search {
  border-top-left-radius: 0;
  height: 50px;
  box-sizing: border-box;
}

.tabs .button[id=tab-search].inuse {
  background-image: url('../../assets/svgs/icon-search-grey.svg'), url('../../assets/svgs/icon-inuse.svg');
  background-repeat: no-repeat, no-repeat;
  background-position: center, right 8px top 6px;
  background-size: 18px 18px, auto;
}

.tabs .button > .chevron::before {
  border-style: solid;
  border-width: 1.5px 1.5px 0 0;
  content: '';
  display: inline-block;
  height: 5px;
  left: 6px;
  position: relative;
  top: 3px;
  transform: rotate(135deg);
  vertical-align: top;
  width: 5px;
}

.tabs .button.active > .chevron:before {
  top: 6px;
  transform: rotate(-45deg);
}
