@import "../../styles/colours";

.small {
  font-size: 10px;
}

.startIcon,
.endIcon,
.eventContainer,
.event {
  position: absolute;
  top: 0;
  bottom: 0;
}

.event {
  left: 0;
  right: 0;
}

.LayerContent {
  height: 100%;
  padding: 0;
  margin: 0;
}

.LayerContent,
.LayerContent > * {
  position: absolute;
}

.transfer {
  background-color: $nb-grey-mid-25;
}

.transfer.scrubber {
  border-left: 2px solid $nb-grey-mid-25;
  border-right: 2px solid $nb-grey-mid-25;
}

.pause {
  background-color: $nb-magenta-25;
}

.start,
.ring {
  background-color: $nb-grey-lightest;
}

.bridge {
  background-color: transparent;
}

.start.wholeTimeline,
.ring.wholeTimeline {
  background-color: transparent;
  background-image: url('../../assets/svgs/diagonal-hash.svg');
}

.pause.scrubber {
  border-left: 2px solid $nb-magenta-25;
  border-right: 2px solid $nb-magenta-25;
}

.hold {
  background-color: $nb-grey-mid-25;
}

.hold.scrubber {
  border-left: 2px solid $nb-grey-mid-25;
  border-right: 2px solid $nb-grey-mid-25;
}

.startIcon {
  left: -12px;
}

.scrubber {
  background-color: rgba(0, 0, 0, 0);
}

.endIcon {
  right: -12px;
}

.microClusteredEndIconContainer {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}

.microClusteredEndIcon {
  left: 0;
  right: 0;
}

.markerLines.leftLine {
  border-left: 2px solid black;
}

.markerLines.rightLine {
  border-right: 2px solid black;
}

.routingOverlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.routing {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  border-top: 2px dashed orange;
}

.routing.primary {
  top: 32%;
}

.routing.secondary {
  top: 67%;
}

.iconLevel {
  z-index: 1;
}

.unrecordedTimeLabel {
  position: absolute;
  left: 0;
  top: 45%;
  right: 0;
  padding-top: 4px;
  font-size: 10px;
  color: $nb-grey-primary;
  text-align: center;
}
