@import "../../styles/colours";
@import "../../styles/variables";

.EventTimeline {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.ControlLayer,
.EventLayer,
.LayerContent {
  height: 100%;
  padding: 0;
  margin: 0;
}

.overlayMargin {
  margin-top: 12px;
}

.LayerContent:focus {
  outline: none;
  border: none;
}

.gradientLayer {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.gradientStart {
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    pointer-events: none;
    background-image: linear-gradient(to left, rgba(247, 247, 247, 0), $nb-grey-extra-light);
    width: 7%;
  }
}

.gradientEnd {
  &:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    background-image: linear-gradient(to right, rgba(247, 247, 247, 0), $nb-grey-extra-light);
    width: 7%;
  }
}

.ControlLayer {
  position: absolute;
  right: 100%;
  background-color: $nb-grey-mid-dark;
}

.EventLayer {
  position: relative;
}

.LayerContent,
.LayerContent > * {
  position: absolute;
}

.sentenceMarker {
  display: inline-block;
  position: absolute;
  right: 100%;
  top: 50%;
  height: 34px;
  width: 200px;
}

.sentenceMarker:after {
  content: attr(data-marker-title);
  position: absolute;
  height: 20px;
  color: $nb-grey-primary;
  border-radius: 0 4px 4px 0;
  top: 20%;
  right: 100%;
  margin-top: -10px;
  margin-right: 0;
  text-align: right;
  padding: 0 4px;
  background-color: $nb-white;
  white-space: nowrap;
  padding-left: 25px;
  @include drop-shadow();
}

.sentenceMarker:before {
  content: "";
  position: absolute;
  height: 32px;
  width: 32px;
  border-radius: 16px;
  right: 100%;
  top: 20%;
  margin-top: -16px;
  margin-right: 104px;
  margin-left: -1px;
  background-color: #5c6770;
  background-image: url('../../assets/svgs/icon-user-white.svg');
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 1;
}

.focusSwitcherTop,
.focusSwitcherBottom {
  position: absolute;
  left: 0;
  right: -20px;
}

.sentenceMarkerAvatarTop {
  position: absolute;
  bottom: 36px;
  left: -2px;
  z-index: 1;
}

.focusSwitcherTop {
  top: -14px;
  bottom: 22px;
}

.sentenceMarkerAvatarBottom {
  position: absolute;
  bottom: -34px;
  left: 0;
  z-index: 1;
}

.focusSwitcherBottom {
  top: 6px;
  bottom: 2px;
}

.sentence {
  position: absolute;
}

.sentence.conference {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='10' height='32' viewBox='0 0 10 32'><rect fill='%2364CCC9 x='0' y='0' width='10' height='8' /><circle fill='%2364CCC9' cx='5' cy='16' r='3.5' /><rect fill='%2364CCC9 x='0' y='24' width='10' height='8' /><svg>");
  background-repeat: repeat-x;
  background-size: auto 100%;
}

.top .sentenceMarker {
  right: auto;
  left: 0;
  top: -8px;
  margin-top: -12px;
}

.top .sentenceMarker:after {
  right: auto;
  left: 26px;
  margin-top: -18px;
  margin-right: 0;
  text-align: right;
}

.top .sentenceMarker:before {
  right: auto;
  left: 0;
  margin-top: -24px;
  margin-right: 104px;
}

.bottom .sentenceMarker {
  right: auto;
  left: 0;
  top: auto;
  bottom: -20px;
  margin-bottom: -12px;
  border-left: 2px solid #5c6770;
  border-radius: 0 0 0 16px;
}

.bottom .sentenceMarker:after {
  right: auto;
  left: 26px;
  margin-top: 2px;
  margin-right: 0;
  text-align: right;
}

.bottom .sentenceMarker:before {
  right: auto;
  left: 0;
  margin-top: -4px;
  margin-right: 104px;
}

.playhead {
  height: 100%;
  display: flex;
  flex-flow: column;
  width: 2px;
  margin-left: -1px;
  //background-color: $nb-grey-dark;
}

.playheadTop {
  background-color: $nb-grey-primary;
  flex: 0 1 24px;
}

.playheadBottom {
  background-color: $nb-grey-primary;
  flex: 1 1 auto;
}

.playheadTime {
  flex: 0 1 18px;
  color: $nb-grey-primary;
}

.playheadTime > div {
  background-color: $nb-grey-extra-light;
  padding: 0 3px;
  position: absolute;
  transform: translateX(-50%);
  //top: 5px;
}

.timeStamp {
  height: 100%;
  display: flex;
  flex-flow: column;
  width: 2px;
  margin-left: -1px;
}

.timeStampLine {
  width: 1px; /* Line width */
  background-color: $nb-grey-lightest; /* Line color */
  height: 12%; /* Override in-line if you want specific height. */
  display: inline-block;
}

.timeStampText {
  color: $nb-grey-primary;
  margin-left: -10px;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: $nb-grey-primary;
  opacity: 0.7;
  z-index: 2;
}

.transport-container {
  position: absolute;
  top: 20px;
  bottom: 20px;
  width: 400px;
  left: 50%;
  transform: translateX(-50%);
  background-color: $nb-white;
  color: $nb-grey-primary;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  z-index: 3;
  overflow: hidden;
  @include drop-shadow();
}

.transport-button {
  width: 151px;
  height: 32px;
  margin: 12px 0;
  border-radius: 4px;
  border: none;
  outline: none;
  background-color: #c4d600;
  text-align: left;
  white-space: nowrap;
  padding-left: 40px;
}

.transport-button.start,
.transport-button.restart {
  background-repeat: no-repeat;
  background-position: left 8px center;
}

.transport-button.start {
  background-image: url('../../assets/svgs/icon-button-playback.svg');
}

.transport-button.restart {
  background-image: url('../../assets/svgs/icon-button-restart.svg');
}

.overlayContentFlex {
  width: 90%;
  display: flex;
  justify-content: space-around;
  margin-left: 24px;
  margin-right: 24px;
}

.info-box {
  padding-left: 0.5rem;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-around;
}

.info-displaying-text {
  padding-top: 14px;
  display: flex;
  text-align: right;
  justify-content: flex-end;
  width: inherit;
}

.channelLines {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.channelLabel {
  display: flex;
  width: 92%;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.channelLabel div {
  padding-left: 12px;
}

.tinyNumber {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 8px;
  margin-top: -8px;
  display: inline-block;
  font-size: 8px;
  text-align: center;
}

.hideEventTimeline {
  position: absolute;
  top: 40%;
  object-fit: contain;
  right: 15px;
  z-index: 9999;
}

.showEventTimeline {
  position: relative;
  height: 18px;
  overflow: hidden;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 12px;
  margin-top: $baseline;
  margin-left: 4px;
  border-radius: 8px;
}

.showEventTimelineContent {
  background: $nb-grey-primary;
  color: #FFFFFF;
  padding: 8px;
  border-radius: 4px;
}

.showIcon {
  margin-left: 8px;
  vertical-align: middle;
  object-fit: contain;
}

.pauseSkipIcon {
  position: absolute;
  top: 47%;
  transform: translateX(-50%);
}

.progressBarContainer {
  text-align: center;
  font-size: 10px;
  color: $nb-grey-primary;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translateX(-50%);
}

.progressBarOuter {
  margin: 0;
  width: 164px;
  height: 8px;
  border-radius: 4px;
  border: solid 1px $nb-grey-primary;
  background-color: $nb-grey-primary;
}

.progressBar {
  box-sizing: border-box;
  padding: 1px;
  position: absolute;
  top: 2px;
  height: 7px;
  border-radius: 3px;
  border: solid 1px $nb-green-light;
  background-color: $nb-green-light;
}

.closeButton {
  position: absolute;
  top: 4px;
  right: 4px;
  border: none;
  background-image: url("../../assets/svgs/close-grey-primary.svg");
  border-radius: 9px;
  width: 18px;
  height: 18px;
}

.closeButton:active {
  transform: scale(0.95);
}

.unreadNotes {
  background-color: $nb-white;
  width: 100%;
  margin-bottom: -6px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 4px;

  .notificationBell {
    background-color: transparent;
    background-image: url("../../assets/svgs/icon-notification.svg");
    background-position: center center;
    background-repeat: no-repeat;
    width: 18px;
    height: 18px;
    margin-left: 31px;
  }

  .notificationCount {
    flex-grow: 1;
    color: $nb-grey-primary;
    font-size: 12px;
    padding-left: 12px;
  }

  .reviewButton {
    cursor: pointer;
    outline: none;
    text-transform: capitalize;
    border: none;
    background-color: transparent;
    color: $nb-blue-mid;
    margin-right: 24px;
  }
}
