@import "../../styles/colours";
@import "../../styles/variables";

.header,
.summaryBar,
.summaryLines,
.summaryLabels {
  display: flex;
  flex-direction: row;
}

.summaryLabels {
  justify-content: space-between;
  text-align: center;
}

.summaryLabels .label {
  padding-left: 8px;
  padding-right: 8px;
}

.summaryLines {
  position: relative;
  height: 16px;
}

.summaryLines .line {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
}

.summaryLines .line .group {
  stroke: $nb-grey-light;
  stroke-width: 2;
}

.summaryLines .line.flip .group {
  transform: scale(-1, 1) translateX(-100%);
}

.header {
  justify-content: space-between;
  font-size: 12px;
}

.section {
  color: $nb-grey-primary;
  padding: 12px 0;
  border-top: 2px solid $nb-grey-mid-dark;
}

.section:first-child {
  padding-top: 0;
  border-top: none;
}

.section:last-child {
  padding-bottom: 0;
}

.sectionColumns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sectionColumns div {
  width: 100%;
}

.summaryBar {
  border-radius: 8px;
  overflow: hidden;
}

.summaryBar {
  background-color: $nb-grey-lightest;
  text-align: center;
  color: white;
}

.summaryBar {
  margin: 16px 0 0 0;
}

.summaryBar {
  height: 16px;
}

.summaryLabels {
  color: $nb-grey-primary;
}

.summaryBar .talking {
  background-color: $nb-green-light;
}

.summaryBar .listening {
  background-color: $nb-grey-dark;
}

.summaryBar .overtalk {
  background-color: $nb-grey-mid-dark;
}

.summaryBar .silent {
  background-color: $nb-grey-mid-light;
}

.summaryBar .holding {
  background-color: $nb-grey-light;
}

.summaryBar .queuing {
  background-color: $nb-grey-lighter;
}

.summaryBar .routing {
  background-color: $nb-grey-lightest;
}

.summaryBar .ringing {
  background-color: $nb-grey-extra-light;
}

.agent {
  color: $nb-white;
}

.customer {
  color: $nb-grey-primary;
}

.header .headerItem {
  border-left: 2px solid $nb-grey-mid-dark;
  padding: 0 12px;
  white-space: nowrap;
}

.header .headerItem:first-child {
  padding-left: 0;
  border-left: none;
}

.header .headerItem:last-child {
  border-left: none;
  width: 100%;
  text-align: right;
  padding-right: 0;
}

.buttonShowLess,
.buttonShowMore {
  outline: none;
  border: none;
  color: $nb-grey-primary;
  background-color: transparent;
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  padding: 0 20px 0 0;
  font-size: 12px;
}

.buttonShowMore {
  background-image: url("../../assets/svgs/icon-plus-green.svg");
}

.buttonShowLess {
  background-image: url("../../assets/svgs/icon-minus-green.svg");
}

.buttonShowLess:active,
.buttonShowMore:active {
  color: $nb-grey-light;
}

.buttonShowMore:before {
  content: "Show More";
}

.buttonShowLess:before {
  content: "Show Less";
}

.metricsSection {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.metricsTable,
.totals {
  display: flex;
  flex-direction: column;
}

.metricsTable {
  flex: 0 0 50%;
  justify-content: space-between;
}

.totals {
  text-align: center;
  flex: 0 0 25%;
  justify-content: space-around;
  margin: 12px 0;
}

.metricsTable div {
  padding: 2px;
}

.metricsTable .figure {
  text-align: center;
}

.rowTitle {
  flex: 0 0 40%;
}

.bigDigits {
  font-size: 36px;
  font-weight: bold;
}
