@import "../../styles/colours";
@import "../../styles/variables";

.TranscriptionNotes {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;
  @include drop-shadow();
}

.NewNoteSection {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  background: $nb-grey-mid-25;
}

.NewNoteSection > div {
  display: flex;
  align-items: center;
}

.newNoteContainer {
  outline: none;
  height: 40px;
  margin: 8px;
  background-color: $nb-green-light;
  border-radius: 4px;
  border: 1px solid  $nb-grey-light;
  cursor: pointer;
  display: flex;
}

.newNoteText {
  flex-grow: 1;
  height: 22px;
  padding: 8px;
  line-height: 22px;
  white-space: nowrap;
}

.newNoteIcon {
  width: 24px;
  height: 24px;
  padding: 8px;
  background-color: $nb-green-light;
  border-radius: 4px 0 0 4px;
}

.checkboxContainer {
  box-sizing: border-box;
  margin: 8px;
  width: 150px;
  display: flex;
  justify-content: space-between;
}


.checkboxLabel {
  box-sizing: border-box;
  color: $nb-grey-primary;
  align-self: center;
}

.checkbox {
  outline: none;
  float: right;
  appearance: none;
  background-color: $nb-white;
  border: 2px solid $nb-grey-lighter;
  padding: 5px;
  border-radius: 2px;
  display: inline-block;
  position: relative;
}

.checkbox:checked {
  border: 2px solid $nb-grey-mid-light;
}

.checkbox:checked:after {
  content: '';
  position: absolute;
  width: 6px;
  height: 3px;
  top: 2px;
  left: 1px;
  border: 2px solid $nb-grey-mid-light;
  border-top: none;
  border-right: none;
  border-radius: 1px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.checkbox::after {
  clear: right;
}

.note {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: $nb-grey-mid-25;
  margin: 8px;
  border-radius: 4px;
  outline:none
}

.note .heading {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-weight: bold;
  font-size: 12px;
  color: $nb-grey-mid-dark;
  margin: 8px 8px 0 8px;
}

.note .heading .time {
  margin-right: 4px;
}

.noteText {
  flex-grow: 1;
  overflow: hidden;
  margin: 0 8px 8px 8px;
}

.editButton {
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  width: 24px;
  height: 24px;
  background-color: $nb-grey-light;
  background-image: url('../../assets/svgs/icon-edit.svg');
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 0 2px 0 2px;
}

.bottomMenuSpacer {
  height: 48px;
}
