@import "../../styles/colours";
@import "../../styles/variables";

.Scrubber {
  border-radius: 4px;
  height: 48px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  font-size: 12px;
  position: relative;
  background-color: $nb-white;
  @include drop-shadow();
}

.Scrubber:not(:first-child) {
  margin-top: 2px;
}

.Scrubber:last-child {
  margin-bottom: $baseline;
}

.info-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 10rem;
  background-color: $nb-grey-mid-dark;
  color: $nb-grey-mid-dark;
}

.info-section > div {
  height: 100%;
  flex-grow: 1;
  color: $nb-grey-mid-dark;
  background-color: $nb-grey-light;
  border-right: 2px solid $nb-grey-mid-dark;
}

.timeline-scrubber-container {
  position: relative;
  flex-grow: 1;
  background: $nb-grey-primary;
  display: flex;
  align-items: stretch;
  overflow: hidden;
  user-select: none;
  padding-left: 8px;
  padding-right: 8px;
}

.timeline-scrubber {
  position: relative;
  width: 100%;
}

.timeline-scrubber:focus {
  outline: none;
}

.timeline-scrubber > * {
  pointer-events: none;
}

.scrub-window,
.scrub-control,
.scrub-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.scrub-control {
  pointer-events: none;
}

.scrub-window {
  background-color: $nb-grey-extra-light;
}

.scrub-control .scrub-handle {
  background-color: $nb-green-light;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.scrub-handle:before {
  height: 16px;
  border-radius: 1px;
  width: 2px;
  background-color: $nb-white;
}

.scrub-handle.left {
  left: -8px;
}

.scrub-handle.right {
  right: -8px;
}

.empty {
  text-align: center;
  display: block;
  padding-top: 15px;
  background-color: $nb-white;
  color: $nb-grey-primary;
}

.hideScrubber {
  position: absolute;
  top: 12px;
  object-fit: contain;
  right: 10px;
  z-index: 9999;
}

.showScrubber {
  position: relative;
  height: 18px;
  overflow: hidden;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 12px;
  margin-top: $baseline;
  margin-right: 4px;
  border-radius: 8px;
}

.showScrubberContent {
  background: $nb-grey-primary;
  color: #FFFFFF;
  padding: 8px;
  border-radius: 4px;

}

.showIcon {
  margin-left: 8px;
  vertical-align: middle;
  object-fit: contain;
}
