@import "../../styles/colours";
@import "../../styles/variables";

.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(73, 83, 92, 0.5);
  z-index: 11,
}

.NoteModal {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 100%;
  top: 15%;
  max-height: 70%;
  left: 50%;
  transform: translate(-50%, 0);
  background: $nb-grey-lightest2;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
}

.modalHeader {
  margin: 24px 24px 16px 24px;
  text-align: center;
  color: $nb-grey-primary
}

.modalHeader .title {
  height: 20px;
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 4px;
}

.modalBody {
  width: 100%;
  box-sizing: border-box;
  overflow: scroll;
}

.modalBodyDisabled {
  opacity: 0.6;
}

.noteDetailsContainer {
  display: flex;
  margin: 0 12px;
  color: $nb-grey-primary;
}

.threadContainer {
  margin: 0 16px;
}

.detailColumn {
  flex: 1;
  margin: 0 12px 16px 12px;
}

.detailColumn > div:first-of-type {
  margin-bottom: 8px;
}

#timeDetailColumn {
  min-width: 174px;
  white-space: nowrap;
}

#userDetailColumn {
  white-space: nowrap;
}

#relatingToDetailColumn {
  min-width: 120px;
  white-space: no-wrap;
}

#polarityDetailColumn {
  max-width: 100px;
  white-space: nowrap;
}

.timeInput {
  outline: none;
  width: 100%;
}

.dropdown {
  user-select: none;
  cursor: pointer;
  position: relative;
  height: 34px;
  width: 100%;
  padding: 8px;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  box-sizing: border-box;
  background-image: url('../../assets/svgs/down-chevron-grey.svg');
  background-repeat: no-repeat, repeat;
  background-position: right 8px top 50%, 0 0;
  background-size: 24px auto, 100%;
}

.timeDropdown {
  background-image: none;
  text-align: center;
}

.dropdownButton {
  display: block;
  color: $nb-grey-primary;
}

.dropdownButtonWithIcon {
  display: flex;
}

.dropdownButtonIcon {
  margin-right: 18px;
  margin-top: -4px;
}

#typeToggle:checked ~ .dropdownContent {
  display: block;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
}

#timeToggle:checked ~ .dropdownContent {
  display: block;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  width: 100%;
  overflow: visible;
  background-color: white;
  justify-content: center;
}

.dropdownContent {
  display: block;
  position: absolute;
  top: 32px;
  width: 100%;
  left: 0;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  overflow: visible;
  justify-content: center;
  @include drop-shadow();
}

.dropdownItem {
  display: block;
  width: 100%;
  height: 41px;
  background-color: $nb-grey-lightest2;
  border-style: solid;
  border-color: $nb-grey-primary;
  border-width: 0 0 1px 0;
  box-sizing: border-box;
  padding: 8px 8px 8px 8px;
  color: $nb-grey-primary;
  vertical-align: middle;
  line-height: 2;
  white-space: nowrap;
  overflow: hidden;
}

.dropdownItem:hover {
  background-color: #e5e6e7;
}

.dropdownContent .dropdownItem:last-child {
  border-bottom: 0;
}

.dropdownItemWithIcon {
  display: flex;
}

.dropdownItemIcon {
  margin-right: 18px;
}

.dropdownBackground {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: transparent;
}

.noteTextContainer {
  padding: 0 24px;
  box-sizing: border-box;
  width: 100%;
}

.noteDetails {
  box-sizing: border-box;
  outline: none;
  width: 100%;
  background-color: $nb-grey-lightest2;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  resize: none;
  color: $nb-grey-primary;
  padding: 16px;
}

.shareContainer {
  padding: 0 24px;
  box-sizing: border-box;
  width: 100%;
  color: $nb-grey-primary;
}

.modalFooter {
  padding: 24px;
  text-align: right;
}

.closeButton {
  color: white;
  cursor: pointer;
  float: right;
  font-size: 30px;
  margin: 0;
}

.closeButton:hover {
  color: black;
}

.cancelButton, .addButton, .deleteButton {
  height: 32px;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 2px 28px;
  background-color: $nb-grey-lightest2;
}

.cancelButton {
  color: $nb-blue-mid;
}

.deleteButton {
  float: left;
  color: $nb-red;
  padding: 2px;
}

.addButton {
  background-color: $nb-green-light;
  border-radius: 4px;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.timePicker {
  display: flex;
  flex-direction: row;
  padding: 30px 38px;
  justify-content: center;
  align-items: center;
  border: solid 1px #5c6770;
  background-color: $nb-grey-lightest2;
  border-radius: 4px;
  margin-top: 7px;
  margin-left: -8px;
  margin-right: -8px;
  @include drop-shadow();
}

.numberPicker {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.numberInput {
  width: 24px;
  height: 24px;
  border: 1px solid $nb-grey-primary;
  border-radius: 2px;
  text-align: center;
  margin: 4px;
  background-color: $nb-grey-lightest2;
}

.upButton {
  width: 0;
  height: 0;
  padding: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 0;
  border-bottom: 8px solid $nb-grey-light;
  background-color: $nb-grey-lightest2;
}

.downButton {
  width: 0;
  height: 0;
  padding: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 0;
  border-top: 8px solid $nb-grey-light;
  background-color: $nb-grey-lightest2;
}

.checkboxContainer {
  box-sizing: border-box;
}

.checkboxLabel {
  width: 100%;
  box-sizing: border-box;
  color: $nb-grey-primary;
}

.checkbox {
  outline: none;
  float: right;
  appearance: none;
  background-color: $nb-grey-lightest2;
  border: 2px solid $nb-grey-primary;
  margin: 3px 0 0 0;
  padding: 7px;
  border-radius: 2px;
  display: inline-block;
  position: relative;
}

.checkbox2 {
  float: none;
  margin-left: 10px;
}

.checkbox:checked:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 4px;
  top: 3px;
  left: 1px;
  border: 2px solid $nb-grey-primary;
  border-top: none;
  border-right: none;
  border-radius: 1px;
  -webkit-transform: rotate(-50deg);
  -moz-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  transform: rotate(-50deg);
}

.checkbox::after {
  clear: right;
}

@media (min-width: 576px) {
  .NoteModal {
    max-width: 568px;
  }
}

@media (min-width: 768px) {
  .NoteModal {
    max-width: 630px;
  }
}

@media (min-width: 992px) {
  .NoteModal {
    max-width: 715px;
  }
}

