@import "../../styles/colours";

.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(73, 83, 92, 0.5);
}

.CallRating {
  position: fixed;
  width: 100%;
  top: 15%;
  left: 50%;
  transform: translate(-50%, 0);
  background: $nb-grey-extra-light;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  max-height: calc(100vh - 16%);
  overflow-y: auto;
}

.modalHeader .title {
  height: 20px;
  font-size: 16px;
  line-height: 1.25;
}

.modalBody {
  color: $nb-grey-primary;
  width: 100%;
  box-sizing: border-box;
  padding: 16px 32px;
  text-align: center;
}

.ratingRow {
  display: flex;
  flex-wrap: wrap;
  padding-top: 24px;
  box-sizing: border-box;
  width: 100%;
}

.ratingContainer {
  flex: 1;
  padding: 0 8px;
  min-width: 227px
}

.ratingTitle {
  font-size: 16px;
  color: $nb-grey-primary;
}

.noteDetails {
  box-sizing: border-box;
  outline: none;
  width: 100%;
  background-color: $nb-grey-extra-light;
  border: 1px solid $nb-grey-primary;
  border-radius: 4px;
  resize: none;
  color: $nb-grey-primary;
  padding: 16px;
  text-align: center;
}

.modalHeader {
  margin: 24px 24px 0 24px;
  text-align: center;
  color: $nb-grey-primary
}

@mixin transition($transition-property, $transition-time, $method) {
  -webkit-transition: $transition-property $transition-time $method;
  -moz-transition: $transition-property $transition-time $method;
  -ms-transition: $transition-property $transition-time $method;
  -o-transition: $transition-property $transition-time $method;
  transition: $transition-property $transition-time $method;
}

@mixin circle-shape($width, $background-color: transparent) {
  @include transition(all, .3s, linear);
  width: $width;
  height: $width;
  background-color: $nb-grey-extra-light;
  border: 2px solid $background-color;
  border-radius: 50%;
  margin: 0 2px;
}

.pipRating {
  text-align: center;
  padding: 16px;
}

.rating {
  display: inline-block;
  position: relative;
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    height: 1px;
    width: 1px;
    bottom: 0;
  }
}

.pip {
  @include circle-shape(16px, $nb-grey-light);
  float: right;
}

//with different colors
$colors: $nb-green-dark,
$nb-green-light,
$nb-yellow,
$nb-orange,
$nb-red;
@for $i from length($colors) through 1 {
  .pip#{$i} {
    &.active,
    &.active ~ .pip {
      background: nth($colors, $i);
      opacity: 1;
      &:before,
      &:after {
        background: nth($colors, $i);
        opacity: 1;
      }
    }
  }
  input[type="radio"] + .pip#{$i} {
    cursor: pointer;
    &:hover,
    &:hover ~ .pip {
      background: nth($colors, $i);
      opacity: 0.5;
      &:before,
      &:after {
        background: nth($colors, $i);
        opacity: 0.5;
      }
    }
  }
  .rating:not(:hover) {
    .radio#{$i}:checked ~ .pip {
      background: nth($colors, $i);
      &:before,
      &:after {
        background: nth($colors, $i);
      }
    }
  }
}

.modalFooter {
  padding: 24px;
  text-align: right;
}

.closeButton {
  color: white;
  cursor: pointer;
  float: right;
  font-size: 30px;
  margin: 0;
}

.closeButton:hover {
  color: black;
}

.cancelButton, .finishButton {
  height: 32px;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 2px 32px;
}

.cancelButton {
  background-color: $nb-grey-extra-light;
  color: $nb-blue-mid;
}

.finishButton {
  background-color: $nb-green-light;
  border-radius: 4px;
}

@media (min-width: 576px) {
  .CallRating {
    max-width: 576px;
  }
  .modalBody {
    padding: 16px 98px;
  }
  .pip {
    margin: 0 4px;
  }
}

@media (min-width: 768px) {
  .CallRating {
    max-width: 658px;
  }
  .modalBody {
    padding: 16px 100px;
  }
  .pip {
    margin: 0 8px;
  }
}

@media (min-width: 992px) {
  .CallRating {
    max-width: 832px;
  }
  .modalBody {
    padding: 16px 173px;
  }
}

@media (min-width: 1200px) {
  .CallRating {
    max-width: 1012px;
  }
  .modalBody {
    padding: 16px 173px;
  }
}
