@import "../../styles/colours";

.pill {
  background-color: $nb-grey-lightest;
  border-radius: 8px;
  color: $nb-grey-primary;
  overflow: hidden;
}

.pillModal {
  background-color: $nb-grey-lightest;
  border-radius: 8px;
  color: $nb-grey-primary;
  overflow: hidden;
  margin-top: 12px;
  margin-right: 8px;
  outline: $nb-grey-primary 0px;
}

.pillCategoryModal {
  background-color: $nb-grey-lightest;
  border-radius: 8px;
  color: $nb-grey-mid-light;
  overflow: hidden;
  margin-top: 12px;
  margin-right: 8px;
  outline: $nb-grey-primary 0px;
}

.pillModal:focus,
.pillCategoryModal:focus{
  outline: none;
}

.pill:focus {
  outline: none;
}

.pill:not(:last-child) {
  margin-right: 8px;
}

.pillText {
  line-height: 1;
  margin: 6px 8px 4px 8px;
  pointer-events: none;
}

.pillColourContainer {
  display: flex;
  height: 4px;
}

.pillColourContainer > div {
  flex: 1;
}

.pill[data-metric-selected=true] {
  color: $nb-white;
  background-color: $nb-grey-darkest;
}

.pillModal[data-metric-selected=true] {
  box-shadow: 0 0 1pt 1pt $nb-grey-primary;
  color: $nb-white;
  background-color: $nb-grey-darkest;
}